import axios from "axios";

const api = axios.create({
    headers: {
        'Content-Type': 'multipart/form-data'
    }
})

export const uploadThumbnail = async (image) => {
  const formData = new FormData();
  formData.append("file", image);
  formData.append("upload_preset", "vg6xb0cu");
  formData.append("cloud_name", "dj5smabhr");
  
  const data = await api.post(
    "https://api.cloudinary.com/v1_1/dj5smabhr/image/upload",
    formData
  ).then(({data}) => data)
  return data.url;
};

export const uploadVideo = async (vid) => {
    const formData = new FormData();
    formData.append("file", vid);
    formData.append("upload_preset", "vg6xb0cu");
    formData.append("cloud_name", "dj5smabhr");
    
    const data = await api.post(
      "https://api.cloudinary.com/v1_1/dj5smabhr/video/upload",
      formData
    ).then(({data}) => data)
    return data.url
  };
